import React, {useEffect, useState} from "react";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const options = {
  responsive: true,
  plugins: {
    legend: {
      position: "top",
    },
    title: {
      display: false,
    },
  },
  scales: { y: { max: 105, min: 0, ticks: { beginAtZero: true } } },
};

const loadingChart = {
  data: {
    labels: [...Array(10).keys()].map((x) => "Loading..."),
    datasets: [
      {
        label: "Uptime",
        data: [0],
        borderColor: "grey",
        backgroundColor: "grey",
      },
    ],
  },
};

export default function UptimeChart({entityId}) {
  const [chartType, setChartType] = useState('Hourly')
  const [chart, setChart] = useState(loadingChart)

  function getUptime() {
    setChart(loadingChart)
    fetch(
      `/SAT_BRH/API/entities/${entityId}/uptime/${chartType}`
    )
      .then((res) => res.json())
      .then((result) => {
        setChart(result)
      });
  }

  useEffect(()=> {
    getUptime();
  }, [chartType])

  function toggleSelectedChartType() {
    setChartType(chartType === 'Hourly' ? 'Daily': 'Hourly')
  }

    return (
      <div>
        <h5>
          <span>Uptime</span>
          <a
            className="link"
            style={{ float: "right" }}
            onClick={toggleSelectedChartType}
          >
            {chartType}
          </a>
        </h5>

        <Line
          options={options}
          data={chart}
          width={"656px"}
          height={"328px"}
          style={{
            display: "block",
            boxSizing: "border-box",
            height: "164px",
            width: "328px",
          }}
        />
      </div>
    );
}
